<template>
  <div class="customers container">
    <alert v-if="alert" :message="alert"></alert>
    <h2 class="page-geader">用户信息管理</h2>
    <div class="holder">
      <input type="text" class="form-control" placeholder="搜索用户" v-model="filterInput" />
      <span class="glyphicon glyphicon-search"></span>
    </div>
    <br />
    <table class="table table-striped">
      <thead>
        <tr>
          <th>姓名</th>
          <th>电话</th>
          <th>邮箱</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(customer,i) in filterBy(customers,filterInput)" :key="i">
          <td>{{customer.name}}</td>
          <td>{{customer.phone}}</td>
          <td>{{customer.email}}</td>
          <td>
            <router-link class :to="'/customer/'+customer.id">
              <span class="glyphicon glyphicon-eye-open btn btn-default" title="详情"></span>
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Alert from "./Alert";
export default {
  name: "customers",
  data() {
    return {
      customers: [],
      alert: "",
      filterInput: ""
    };
  },
  components: {
    Alert
  },
  methods: {
    fetchCustomers() {
      this.$http
        .get("https://vcustomers-admin.firebaseio.com/posts.json")
        .then(response => {
          return response.data;
        })
        .then(data => {
          var resArray = [];
          for (let key in data) {
            data[key].id = key;
            resArray.push(data[key]);
            this.customers = resArray;
          }
        });
    },
    filterBy(customers, value) {
      return customers.filter(function(customer) {
        return customer.name.match(value);
      });
    }
  },

  created() {
    if (this.$route.query.alert) {
      this.alert = this.$route.query.alert;
    }
    this.fetchCustomers();
  },
  updated() {
    this.fetchCustomers();
  }
};
</script>


<style scoped>
.holder {
  position: relative;
}
.holder input {
  padding-left: 38px;
}
.glyphicon {
  top: -26px;
  left: 13px;
  font-size: 17px;
  color: #b8b9b9;
}
.btn-default {
  top: 0;
  border: none;
  background: none;
  color: #333333b8;
}
.btn-default:active {
  background: #fff;
  border: none;
  box-shadow: none;
}
.btn-default:hover {
  color: #1ac3d4bd;
}
</style>



