<template>
  <div class="edit container">
    <Alert v-if="alert" :message="alert"></Alert>
    <h1 class="page-herder">编辑用户</h1>
    <form v-on:submit="updateCustomer">
      <div class="well">
        <h4>用户信息</h4>
        <div class="form-group">
          <label for>姓名</label>
          <input type="text" class="form-control" placeholder="name" v-model="customer.name" />
        </div>
        <div class="form-group">
          <label for>电话</label>
          <input type="text" class="form-control" placeholder="phone" v-model="customer.phone" />
        </div>
        <div class="form-group">
          <label for>邮箱</label>
          <input type="text" class="form-control" placeholder="email" v-model="customer.email" />
        </div>
        <div class="form-group">
          <label for>学历</label>
          <input
            type="text"
            class="form-control"
            placeholder="education"
            v-model="customer.education"
          />
        </div>
        <div class="form-group">
          <label for>毕业学校</label>
          <input
            type="text"
            class="form-control"
            placeholder="graduationschool"
            v-model="customer.graduationschool"
          />
        </div>
        <div class="form-group">
          <label for>职业</label>
          <input
            type="text"
            class="form-control"
            placeholder="profession"
            v-model="customer.profession"
          />
        </div>
        <div class="form-group">
          <label for>个人简介</label>
          <textarea name id class="form-control" rows="10" v-model="customer.profile"></textarea>
        </div>
        <button type="submit" class="btn btn-primary">提交编辑</button>
      </div>
    </form>
  </div>
</template>

<script>
import Alert from "./Alert";
export default {
  name: "add",
  data() {
    return {
      customer: {},
      alert: ""
    };
  },
  components: {
    Alert
  },
  methods: {
    fetchCustomer(id) {
      this.$http
        .get("https://vcustomers-admin.firebaseio.com/posts/" + id + ".json")
        .then(response => {
          this.customer = response.data;
        });
    },
    updateCustomer(e) {
      // console.log(111);
      if (!this.customer.name || !this.customer.phone || !this.customer.email) {
        this.alert = "姓名、电话、邮箱不能为空!!";
      } else {
        var updateCustomer = {
          name: this.customer.name,
          phone: this.customer.phone,
          email: this.customer.email,
          education: this.customer.education,
          graduationschool: this.customer.graduationschool,
          profession: this.customer.profession,
          profile: this.customer.profile
        };
        this.$http
          .put(
            "https://vcustomers-admin.firebaseio.com/posts/" +
              this.$route.params.id +
              ".json",
            updateCustomer
          )
          .then(response => {
            // 添加成功后跳转到指定路径
            this.$router.push({ path: "/", query: { alert: "用更新加成功!" } });
          });

        e.preventDefault();
      }
      e.preventDefault();
    }
  },
  created() {
    this.fetchCustomer(this.$route.params.id);
  }
};
</script>


<style scoped>
</style>



