<template>
  <div class="customerdetails container">
    <router-link to="/" class="btn btn-default">返回</router-link>
    <h1 class="page-header">
      {{customer.name}}
      <span class="pull-right">
        <router-link :to="'/edit/'+this.$route.params.id" class="btn btn-primary">编辑</router-link>
        <button class="btn btn-danger" @click="deleteCustomer(id)">删除</button>
      </span>
    </h1>
    <ul class="list-group">
      <li class="list-group-item">
        <span class="glyphicon glyphicon-earphone">{{customer.phone}}</span>
      </li>
      <li class="list-group-item">
        <span class="glyphicon glyphicon-envelope">{{customer.email}}</span>
      </li>
    </ul>

    <ul class="list-group">
      <li class="list-group-item">
        <span class="glyphicon glyphicon-tasks">{{customer.education}}</span>
      </li>
      <li class="list-group-item">
        <span class="glyphicon glyphicon-bookmark">{{customer.graduationschool}}</span>
      </li>
      <li class="list-group-item">
        <span class="glyphicon glyphicon-lock">{{customer.profession}}</span>
      </li>
      <li class="list-group-item">
        <span class="glyphicon glyphicon-pencil glyphicon-last">{{customer.profile}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "customerdetails",
  data() {
    return {
      customer: "",
      id: ""
    };
  },
  methods: {
    fetchCustomers(id) {
      this.$http
        .get("https://vcustomers-admin.firebaseio.com/posts/" + id + ".json")
        .then(response => {
          this.customer = response.data;
        });
    },
    deleteCustomer(id) {
      var admin = prompt("请输入超管秘钥!", "");
      if (admin === "admin") {
        this.$http
          .delete(
            "https://vcustomers-admin.firebaseio.com/posts/" + id + ".json"
          )
          .then(response => {
            this.$router.push({ path: "/", query: { alert: "用户删除成功!" } });
          });
      } else {
        alert("秘钥错误,请联系管理员QQ:452566060");
      }
    }
  },
  created() {
    this.fetchCustomers(this.$route.params.id);
    this.id = this.$route.params.id;
  }
};
</script>


<style scoped>
.list-group-item span::before {
  padding-right: 10px;
}
.glyphicon-last {
  line-height: 24px;
}
</style>



